<template>
  <div id="mgrAddMain">
    <el-dialog title="" :visible.sync="subStatus" @close="fun(tname)">
      
      <span style="color:deepskyblue">{{tableRow.fullname}}</span>
      
      <div slot="footer" class="dialog-footer">
        <el-button @click="subStatus = false"> </el-button>
        <el-button type="primary" @click="deleteDept"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { http, dept } from "@/api/api.js";
export default {
  mounted() {},
  data() {
    return {
      subStatus: this.status, //
      addForm: {
        deptIds: this.tableRow.roleid
      }
    };
  },
  props: ["status", "fun", "tname", "tableRow", "refresh"],
  watch: {
    status() {
      this.subStatus = this.status;
    }
  },
  methods: {
    deleteDept() {
      if (this.tableRow.status != 3) {
        this.$http
          .delete(http + dept, {
            params: {
              id: this.tableRow.id
            }
          })
          .then(
            val => {
              if (val.data.msg === "") {
                this.subStatus = false;
                
                // 
                this.refresh();
                this.$message.success("");
              }
            },
            err => {
              this.$message.error(err.data.message);
            }
          );
      } else {
        this.$message.error("");
      }
    }
  }
};
</script>

