<template>
  <div id="menuAddMain">
    <el-dialog title="" :visible.sync="subStatus" @close="fun(tname)">
      <el-alert
        v-if="tableRow===''"
        title=""
        type="info"
        show-icon
        :closable="false"
        style="margin:10px"
      ></el-alert>
      <el-alert
        v-else
        :title="' '+tableRow.name+' '"
        type="info"
        show-icon
        :closable="false"
        style="margin:10px"
      ></el-alert>
      <el-form ref="form" :model="addForm" label-width="100px" v-if="addForm">
        <el-form-item label="">
          <el-input v-model="addForm.name"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="addForm.code"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="addForm.component"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="addForm.url"></el-input>
        </el-form-item>
         <el-form-item label="">
          <el-radio-group v-model="addForm.status">
            <el-radio label="0"></el-radio>
            <el-radio label="1"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="">
          <el-radio-group v-model="addForm.isMenu">
            <el-radio label="0"></el-radio>
            <el-radio label="1"></el-radio>
          </el-radio-group>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="subStatus = false"> </el-button>
        <el-button type="primary" @click="upDept"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { http, menu } from "@/api/api.js";
export default {
  data() {
    return {
      subStatus: this.status, //
      addForm: {
        name:'',
        code:'',
        component:'',
        url:'',
        status:'',
        isMenu:''
      },
      deptOptions: "",
      props: {
        value: "id",
        label: "simplename",
        children: "children"
      }
    };
  },
  props: ["status", "fun", "tname", "refresh", "tableRow"],
  watch: {
    status() {
      this.subStatus = this.status;
    }
  },
  methods: {
    // 
    upDept() {
      //
      let itemFlag = true;
      for (let i in this.addForm) {
        if (this.addForm[i] == "") {
          itemFlag = false;
        }
      }
      if (itemFlag) {
        // pcode
        if(this.tableRow == ''){
          this.addForm.pcode = 0
        }else{
          this.addForm.pcode = this.tableRow.code 
        }

        //
        this.addForm.num = 0;

        this.$http
          .post(http + menu, JSON.stringify(this.addForm), {
            emulateJSON: true
          })
          .then(
            val => {
              if (val.data.success) {
                //
                this.subStatus = false;
                for (let i in this.addForm) {
                  this.addForm[i] = "";
                }
                // 
                this.refresh();
                this.$message.success("");
              }
            },
            err => {
              this.$message.error(err.data.message);
            }
          );
        delete this.addForm.pcode;
        delete this.addForm.pid;
      } else {
        this.$message.error("");
      }
    }
  }
};
</script>

