<template>
  <div id="info">
    <p>{{$store.state.userInfo.info.profile.account}}</p>
    <p>
      
      <a @click="dialogState = true" style="color:deepskyblue"></a>
    </p>
    <p>{{$store.state.userInfo.info.profile.name}}</p>
    <p>{{$store.state.userInfo.info.profile.birthday}}</p>
    <p>{{$store.state.userInfo.info.profile.sex===1?'':''}}</p>
    <p>{{$store.state.userInfo.info.profile.email}}</p>
    <p>{{$store.state.userInfo.info.profile.phone}}</p>
    <p>{{$store.state.userInfo.info.profile.dept}}</p>
    <p>
      <el-button type="danger" @click="clearOut"></el-button>
    </p>
    <setpass :dialogState="dialogState" :closeState='closeState'></setpass>
  </div>
</template>

<script>
import setpass from "./set.vue";
export default {
  data() {
    return {
      dialogState: false
    };
  },
  methods: {
    clearOut() {
      localStorage.clear();
      location.href = "/login";
    },
    //
    closeState(){
      this.dialogState = false
    }
  },
  components: {
    setpass
    }
};
</script>

