<template>
  <div id="deptAddMain">
    <el-dialog title="" :visible.sync="subStatus" @close="fun(tname)">
      <el-alert
        v-if="tableRow===''"
        title=","
        type="info"
        show-icon
        :closable="false"
        style="margin:10px"
      ></el-alert>
      <el-alert
        v-else
        :title="' '+tableRow.fullname+' ,'"
        type="info"
        show-icon
        :closable="false"
        style="margin:10px"
      ></el-alert>
      <el-form ref="form" :model="addForm" label-width="80px" v-if="addForm">
        <el-form-item label="">
          <el-input v-model="addForm.fullname"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="addForm.simplename"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="subStatus = false"> </el-button>
        <el-button type="primary" @click="upDept"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { http, deptList, dept } from "@/api/api.js";
export default {
  data() {
    return {
      subStatus: this.status, //
      addForm: {
        simplename: "", //
        fullname: "" //
      },
      deptOptions: "",
      props: {
        value: "id",
        label: "simplename",
        children: "children"
      }
    };
  },
  props: ["status", "fun", "tname", "refresh", "tableRow"],
  watch: {
    status() {
      this.subStatus = this.status;
    }
  },
  methods: {
    // 
    upDept() {
      //
      let itemFlag = true;
      for (let i in this.addForm) {
        if (this.addForm[i] == "") {
          itemFlag = false;
        }
      }
      if (itemFlag) {
        
        //pid
        if (this.tableRow == "") {
          this.addForm.pid = 0;
        } else {
          this.addForm.pid = this.tableRow.id;
        }
        //
        this.addForm.num = 0;

        this.$http
          .post(http + dept, JSON.stringify(this.addForm), {
            emulateJSON: true
          })
          .then(
            val => {
              if (val.data.success) {
                //
                this.subStatus = false;
                for (let i in this.addForm) {
                  this.addForm[i] = "";
                }
                // 
                this.refresh();
                this.$message.success("");
              }
            },
            err => {
              this.$message.error(err.data.message);
            }
          );
        delete this.addForm.num;
        delete this.addForm.pid;
      } else {
        this.$message.error("");
      }
    },
    setDate(t) {
      // 
      var newTime = new Date(t);
      return t.getFullYear() + "-" + (t.getMonth() + 1) + "-" + t.getDate();
    }
  }
};
</script>

