<template>
  <div id="roleAddMain">
    <el-dialog 
    title="" 
    :visible.sync="subStatus" 
    @close="fun(tname)">
      <el-form ref="form" :model="addForm" label-width="80px" v-if="deptOptions">
        <el-form-item label="">
          <el-input v-model="addForm.name"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="addForm.tips"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-cascader
            v-model="addForm.deptid"
            :options="deptOptions"
            :show-all-levels="false"
            :props="props"
          ></el-cascader>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="subStatus = false"> </el-button>
        <el-button type="primary" @click="upRole"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { http, deptList, role } from "@/api/api.js";
export default {
  mounted() {
    this.getDeptLlist();
    console.log()
  },
  data() {
    return {
      subStatus: this.status, //
      addForm: {
        tips: "",
        name: "",
        deptid: ""
      },
      deptOptions: "",
      props: {
        value: "id",
        label: "simplename",
        children: "children"
      }
    };
  },
  props: ["status", "fun", "tname", "refresh"],
  watch: {
    status() {
      this.subStatus = this.status;
    }
  },
  methods: {
    // 
    getDeptLlist() {
      this.$http.get(http + deptList).then(
        val => {
          this.deptOptions = this.deptNullClear(val.data.data);
        },
        err => {}
      );
    },
    // 
    deptNullClear(v) {
      for (var i = 0; i < v.length; i++) {
        if (v[i].children.length == 0) {
          v[i].children = null;
        } else {
          this.deptNullClear(v[i].children);
        }
      }
      return v;
    },
    // 
    upRole() {
      let itemFlag = true;
      for (let i in this.addForm) {
        if (this.addForm[i] == "") {
          itemFlag = false;
        }
      }
      if (itemFlag) {
        // id
        this.addForm.deptid = this.addForm.deptid[
          this.addForm.deptid.length - 1
        ];

        //pid
        this.addForm.pid = this.$store.state.userInfo.info.profile.id
        //
        this.addForm.num = 0
        this.$http.post(http + role, JSON.stringify(this.addForm) , { emulateJSON: true }).then(
          val => {
            if (val.data.msg === "") {
              //
              this.subStatus = false;
              for (let i in this.addForm) {
                this.addForm[i] = "";
              }
              // 
              this.refresh();
              this.$message.success("");
            }
          },
          err => {
            this.$message.error(err.data.message);
          })
          delete this.addForm.num
          delete this.addForm.pid
          ;
      } else {
        this.$message.error("");
      }
    },
    setDate(t) {
      // 
      var newTime = new Date(t);
      return t.getFullYear() + "-" + (t.getMonth() + 1) + "-" + t.getDate();
    }
  }
};
</script>

